<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process168"></a>

<p><br></p>
<p><span class="rvts17">自由的味道（</span><span class="rvts50">A Taste of Freedom</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]:</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:480,,Y:740</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：脱水的不死族（</span><span class="rvts20">Desiccated Undead</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">救出脱水的不死族，在它一直不满足的情况下，杀掉它</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1162.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">探索失事洞穴，并打败了莫德斯之后，可以在他的房间深处，找到一个隐藏的地下室。进入地下室可以找到一块非同寻常的宝石。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1163.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">这块宝石位于一个死去矮人的身上，尽可能一次拿到，之后在返回的路上会遭遇沉船机关。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1164.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">返回路上遇到的沉船机关，我们会遭遇一条鲨鱼，击杀它，摆脱困境。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1165.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">再次来到之前</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">支线中被困的两个矮人的地下室（详情参见</span><span class="rvts20"></span><span class="rvts21">浮木阴影</span><span class="rvts20"></span><span class="rvts21">支线），使用宝石可以开启石门，里面有一个秘密通道通往脱水不死族的墓室。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1166.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">接着便是另一道魔法门的密码解密。有一个立体的符文会呈现在眼前（需要与这个区域的一只死亡的老鼠的灵魂交谈才可以获得）。</span>
<br><span class="rvts21">只需按照符文的要求，使与其对应的石阶机关产生一致的效果便会使门打开。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1167.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该解密就像是</span><span class="rvts20"></span><span class="rvts21">扫雷</span><span class="rvts20"></span><span class="rvts21">的逆向思维版，如图明知要站到以下这些石阶上，便可以打开该魔法石门。如果队伍中角色不足，可以使用花瓶或石罐进行填充。</span>
<br><span class="rvts21">秘门一旦被打开，就会永久生效，无需再次激活。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1168.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">解救脱水的干尸会面临一场骷髅大战。在战斗到第二轮时，脱水干尸会帮助我们一起打败骷髅士兵。</span>
<br><span class="rvts21">（如果企图先打碎瓶罐，减少遇敌的骷髅士兵数量，那么最终脱水干尸将不会给我们额外的技能书奖励）</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1169.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">如果选择释放脱水的不死族，它会去寻找更多的源力，来满足饥渴的欲望。（其实就是去杀更多的人）</span>
<br><span class="rvts21">说到这里，强烈建议先放过它。因为之后对于我们将会拥有额外的收益。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1170.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">释放过脱水不死族后，它会朝浮木城西门走去，并且会把西门处的两个魔导师和两只魔导犬杀掉。然后在坐标</span><span class="rvts20">X:170,Y:227</span><span class="rvts21">的位置，会再次遇到它。</span>
<br><span class="rvts21">经过谈话，可以选择杀掉它，或让它汲取角色的一个源力值，免得它继续伤害无辜。强烈建议继续放过它，它会朝西北方向继续徘徊。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1171.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">最后一次看到脱水不死族是靠近回廊森林传送点的位置（坐标</span><span class="rvts20">X:262,Y256</span><span class="rvts21">）。这是最后一次看到该不死族，它会要求使用鲜血祭奠它的墓地。</span>
<br><span class="rvts21">如果这样做的话，会避免一场打斗，并且得到脱水不死族的额外奖励（它会挖出它的灵柩罐，并把宝箱的东西留给你）。</span>
<br><span class="rvts21">也可以选择不献血，并且杀掉它。由于是第三次吸取源力精华，脱水不死族将会变得异常强大。因此建议队伍中的队友尽可能的等级高一些。打败该不死族，可以获得扎克斯的法杖</span><span class="rvts20">揦haxh's Staff of Parastic Decay</span><span class="rvts21">。</span>
<br><span class="rvts21">还有一种方法，就是可以选择在它挖灵柩罐的时候杀掉他，这样既可以拿到宝箱里的宝物，还可以杀掉脱水干尸，并且获得额外的经验。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



